.fft-dashboard-screen {
    --sd-primary-color: #007bff;
    --sd-primary-dark: #004d99;
    --sd-gradient-end: #007bff;
    --sd-gradient-start: #004d99;
    --sd-card-bg: #ffffff;
    --sd-text-light: #ffffff;
    --sd-text-dark: #333333;
    --sd-border-color: rgba(255, 255, 255, 0.5);
    --sd-title-size: 3.5vw;
    --sd-h2-size: 2.5vw;
    --sd-card-title-size: 1.8vw;
    --sd-data-label-size: 1.6vw;
    --sd-data-value-size: 2.2vw;

    background: linear-gradient(135deg, var(--sd-gradient-start) 0%, var(--sd-gradient-end) 100%) !important;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    color: var(--sd-text-dark);
}

.fft-dashboard-header {
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.fft-dashboard-header .fft-dashboard-title {
    color: var(--sd-text-light) !important;
    font-size: var(--sd-title-size);
    font-weight: 700;
    text-align: center;
    letter-spacing: 2px;
    cursor: pointer;
}

.fft-dashboard-header .fft-company-logo {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 5px;
    padding: 0.5rem;
}

.fft-dashboard-header .fft-company-logo img {
    height: 65px;
    object-fit: contain;
}

.fft-dashboard-body {
    height: calc(100vh - 120px);
}

.fft-h-100 {
    height: 100%;
}

.fft-job-cards-container {
    padding-right: 2rem;
    border-right: 2px solid var(--sd-border-color);
}

.fft-job-cards-container h2 {
    font-size: var(--sd-h2-size);
    color: var(--sd-text-light);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.fft-h-job-cards {
    height: calc(100% - 80px);
    overflow-y: auto;
    scrollbar-width: none;
}

.fft-h-job-cards::-webkit-scrollbar {
    display: none;
}

.fft-job-card {
    background-color: var(--sd-card-bg);
    border: 5px solid var(--sd-primary-dark);
    border-radius: 15px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
    min-height: 180px;
}

.fft-job-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
}

.fft-card-header {
    background-color: var(--sd-primary-dark);
    text-align: center;
    padding: 0.5rem 0.5rem;
    border: 2px solid var(--sd-card-bg);
    border-radius: 6px 6px 0 0;
    animation: pulseBackground 6s ease-in-out infinite;
}

.fft-card-title {
    font-size: var(--sd-card-title-size);
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: var(--sd-text-light);
}

.fft-card-body {
    padding: 1rem 1.5rem;
}

.fft-data-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    font-size: var(--sd-data-label-size);
    font-weight: 500;
}

.fft-current-fy-row {
    border-bottom: 1px dashed #ccc;
    font-weight: 700;
}


.fft-current-fy-row .fft-data-value {
    font-size: var(--sd-data-value-size);
}

.fft-data-label{
    display: flex;
    flex-direction: column;
}

.fft-data-label .fft-data-label-fy{
    font-size: 1vw;
    font-weight: 600;
}

.fft-image-course-container {
    height: 100%;
    padding-left: 2rem;
    padding-bottom: 1rem;

}

.fft-image-course-display {
    height: 100%;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    overflow: hidden;
    box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.3);
}

/* Container for the stacked images */
.fft-course-image-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

/* Individual image style for the carousel */
.fft-course-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures image covers the whole area */
    transition: opacity 1.5s ease-in-out; /* Smooth transition for fade effect */
    will-change: opacity; /* Optimisation hint for the browser */
}

/* Active and Inactive states */
.fft-course-image.active {
    opacity: 1;
    z-index: 10; /* Bring active image to the front */
}

.fft-course-image.inactive {
    opacity: 0;
    z-index: 1;
}

@keyframes pulseBackground {
    0% {
        background-color: var(--sd-gradient-start);
    }

    50% {
        background-color: var(--sd-gradient-end);
    }

    100% {
        background-color: var(--sd-primary-dark);
    }
}

@media (max-width: 1920px) {
    .fft-dashboard-screen {
        --sd-card-title-size: 1.6vw;
        --sd-data-label-size: 1.4vw;
        --sd-data-value-size: 1.8vw;
    }
}