@import './footer';
@import './header';

body{
    overflow: hidden;
    width: 100%;
    overflow-y: auto !important;
    scroll-behavior: smooth;
}

.content-body{
    position: relative;
    top: 4.125rem;
    min-height: calc(100vh - 4.125rem);
    margin-bottom: 4.125rem;
    padding-bottom: 0.1rem;
    background-color: $body-bg;
}