.nav-collapse-offcanvas {
  width: 16rem !important;

  .offcanvas-header {
    .brand-logo {
      display: flex;
      align-items: center;
      .logo-icon {
        width: 2rem;
      }
      .logo-text {
        width: 8rem;
      }
    }

    .hamburger-x {
      display: inline-block;
      cursor: pointer;
      width: 1.625rem;
      height: 1.5rem;
      position: relative;
      z-index: 999;
    }

    /* Create the X */
    .hamburger-x .line {
      background: $primary;
      display: block;
      height: 0.125rem;
      width: 1.375rem;
      border-radius: 0.1875rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transition: all 0.3s ease-in-out;
    }

    /* Position the lines to form X */
    .hamburger-x .line:nth-child(1) {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    .hamburger-x .line:nth-child(2) {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  .offcanvas-body {
    padding: 0 !important;
    margin-top: 2rem;
    .header-collapse-nav {
      height: auto;
      width: auto;
      display: flex;
      align-items: start;
      flex-direction: column;
      .nav-item {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 100%;
        padding: 0.5rem 1rem;
        margin-bottom: 1rem;
        text-decoration: none;
        transition: all 0.2s ease-in-out;
        &:hover {
          color: $primary;
          background-color: #e5f1fd !important;
          border-radius: 0.5rem;
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
          .nav-icon svg {
            fill: $primary;
          }
        }
        &.active {
          color: $primary;
          background-color: #e5f1fd !important;
          border-radius: 0.5rem;
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
          .nav-icon svg {
            fill: $primary;
          }
        }
        .nav-icon {
          font-size: 1.5rem;
          color: $primary;
          margin-right: 0.5rem;
        }
        .nav-text {
          flex: 1;
          font-size: 1rem;
          font-weight: 600;
          text-decoration: none;
          text-align: left;
          padding-left: 0.5rem;
          transition: all 0.2s ease-in-out;
        }
      }
    }
  }
}

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 4.125rem;
  width: 100%;
  top: 0;
  padding: 0rem;
  z-index: 1;
  background: #e5f1fd !important;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;

  .header-left {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    .brand-logo {
      display: flex;
      align-items: center;
      .logo-icon {
        width: 3rem;
      }
      .logo-text {
        @include respond("phone") {
          display: none;
        }
        width: 9rem;
      }
    }
    .nav-control {
      display: none;
      @include respond("phone-land") {
        display: block;
        margin-left: 1.5rem;
      }
      .hamburger {
        display: inline-block;
        -webkit-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s;
        width: 1.625rem;
        z-index: 999;
      }

      .hamburger .line {
        background: $primary !important;
        display: block;
        height: 1.5px;
        border-radius: 0.1875rem;
        margin-top: 0.375rem;
        margin-bottom: 0.375rem;
        margin-right: auto;
        -webkit-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
      }
      .hamburger .line:nth-child(1) {
        width: 1.25rem;
      }

      .hamburger .line:nth-child(2) {
        width: 1.25rem;
      }

      .hamburger .line:nth-child(3) {
        width: 1.25rem;
      }

      .hamburger:hover {
        cursor: pointer;
      }
    }
  }
  .header-center {
    @include respond("phone-land") {
      display: none;
    }
    height: 100%;
    padding-left: 1rem;
    padding-right: 1.075rem;
    align-items: center;
    display: flex;
    .header-nav {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: auto;
      padding-left: 1rem;
      padding-right: 1rem;
      .nav-item {
        @include respond("tab-land") {
          margin-left: 0.5rem;
          margin-right: 0.5rem;
          padding: 0rem 0.5rem;
        }
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
        margin-left: 1rem;
        margin-right: 1rem;
        padding: 0rem 1rem;
        text-decoration: none;
        transition: all 0.2s ease-in-out;
        &:hover {
          color: $primary;
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
          .nav-icon svg {
            fill: $primary;
          }
        }
        &.active {
          color: $primary;
          background-color: #e5f1fd !important;
          border-radius: 0.5rem;
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
          .nav-icon svg {
            fill: $primary;
          }
        }
        .nav-icon {
          font-size: 1.5rem;
          color: $primary;
          margin-right: 0.5rem;
        }
        .nav-text {
          font-size: 1rem;
          font-weight: 600;
          text-decoration: none;
          text-align: center;
          transition: all 0.2s ease-in-out;
        }
      }
    }
  }
  .header-right {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    .header-profile {
      position: relative !important;
      .user-profile-icon {
        width: 2.5rem;
        height: 2.5rem;
        margin-right: 7px;
        text-align: center;
        font-size: 1rem;
        background: $primary;
        line-height: 2.5rem;
        color: white;
        border-radius: 50%;
      }
      .header-profile-info {
        @include respond("phone-land") {
          display: none;
        }
        h6 {
          font-size: 0.813rem;
          font-weight: 600;
          margin-bottom: 4px;
          line-height: 1;

          @include respond("laptop") {
            margin-bottom: 4px;
          }
        }
        p {
          font-size: 12px;
          font-weight: 400;
          line-height: 1;
          margin-bottom: 4px;
        }
      }
      .dropdown-menu {
        padding: 0.9375rem 0;
        min-width: 12.5rem;
        z-index: 999;
        transform: scale(0.01);
        transform-origin: center top;
        opacity: 0;
        right: 0 !important;
        transition: transform 0.3s ease, opacity 0.3s ease,
          box-shadow 0.3s ease 0.15s;
        a {
          &:hover,
          &:focus,
          &.active {
            color: var(--primary);
          }
        }
      }
      .dropdown-menu.dropdown-menu-end.show {
        transform: scale(1);
        opacity: 1;
        box-shadow: 3px 3px 30px rgba(118, 96, 168, 0.15);
      }
      img {
        width: 45px;
        @include respond("laptop") {
          width: unset;
        }
      }
    }
  }
}
